<template>
  <div class="invisible" :id="idBloc" @click="toggleModal()"></div>
  <div
    class="modal"
    v-if="displayModal"
    :class="{ decalement: grosseDame }"
    :id="`${idBloc}Modale`"
  >
    <span class="close" @click="toggleModal()"></span>
    <span class="message">
      {{ message }}
    </span>
    <Password
      v-if="motDePasse != null"
      :motDePasse="motDePasse"
      @password-found-event="passwordFound()"
      :column="true"
      class="password"
    />
  </div>
  <img
    class="grosseDame"
    v-if="grosseDame & displayModal"
    src="@/assets/images/Grosse_dame.png"
    id="imgGrosseDame"
  />
  <div class="fondModal" v-if="displayModal"></div>
</template>

<script src="./blocInvisible.js">
</script>

<style>
.invisible {
  opacity: 1;
  cursor: pointer;
  position: absolute;
  z-index: 100;
  top: 0;
  left: 0;
  border: 1px solid red;
  background-color: rgba(255, 89, 89, 0.1);
}

.grosseDame {
  position: absolute;
  z-index: 20000;
  border: 5px solid black;
  border-top-left-radius: 50%;
  border-top-right-radius: 50%;
  background-color: rgb(55, 55, 55);
  left: 5%;
  height: 90% !important;
  top: 5%;
  -webkit-box-shadow: 0px 0px 63px 13px rgba(110, 52, 10, 0.28);
  -moz-box-shadow: 0px 0px 63px 13px rgba(110, 52, 10, 0.28);
  box-shadow: 0px 0px 63px 13px rgba(110, 52, 10, 0.28);
}
.decalement {
  left: 35% !important;
  height: 400px !important;
}
.fondModal,
.modal,
img {
  animation: apparition 0.3s forwards;
}

.password {
  margin-left: 40px;
  margin-top: 20px;
}
.password * {
  text-align: center;
}
.password table {
  min-width: 200px !important;
  width: 300px !important;
}

.modal {
  position: fixed;
  z-index: 10000;
  width: 50%;
  height: 300px;
  left: 25%;
  top: 30%;
  background-color: black;
  background-image: url("~@/assets/images/bgModal.jpg");
  background-size: cover;
  background-repeat: no-repeat;
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
  flex-direction: column;
}
.close {
  position: absolute;
  right: 10px;
  top: 10px;
  width: 32px;
  height: 32px;
  opacity: 1;
  cursor: pointer;
}
.close:hover {
  opacity: 0.6;
}
.close:before,
.close:after {
  position: absolute;
  left: 15px;
  content: " ";
  height: 33px;
  width: 5px;
  background-color: rgb(113, 113, 113);
}
.close:before {
  transform: rotate(45deg);
}
.close:after {
  transform: rotate(-45deg);
}
</style>