
export default {
    name: 'Password',
    data() {
        return {
            motPropose: "",
            isReussi: false,
        }
    },
    props: {
        motDePasse: {
            type: String
        },
        indications: {
            type: String,
            default: null,
        },
        column: {
            default: false
        }
    },
    methods: {
        checkForm() {
            let table = document.getElementById("table_password");
            let tds = table.getElementsByTagName("td");
            let classe;
            if (this.motPropose.toLowerCase() == this.motDePasse.toLowerCase()) {
                // REUSSITE
                classe = "reussite";
                this.isReussi = true;
                this.$emit("password-found-event")
            } else {
                // ECHEC
                classe = "echec";
            }
            tds.forEach(td => {
                td.classList.remove(classe)
                // Permet de réactiver l'animation avec la classe
                void td.offsetWidth; 
                td.classList.add(classe)
            });
        }
    }
}