import Password from '@/components/password/password.vue'
import BlocInvisible from '@/components/blocInvisible/blocInvisible.vue'

export default {
    name: "ChateauIndex",

    components: {
        BlocInvisible,
        Password
    },

    data() {
        return {
            urlImage: require("@/assets/images/chateau.jpg"),
            song: new Audio(require('@/assets/musics/chateau.mp3')),
            instructionsLues: false,
        }
    },

    methods: {
        passwordFound() {
            // On fait tout disparaitre
            let password = document.getElementById("nicheModale");
            password.style.animation = "disparition 3s forwards";
            password = document.getElementsByClassName("fondModal")[0];
            password.style.animation = "disparition 5s forwards";

            let rogue = document.getElementById("rogue");
            rogue.classList.remove("apparition");
            rogue.classList.add("disparition");

            let background = document.getElementById("imgChateau");
            setTimeout(() => {
                background.classList.add("zoomIn")
            }, 2000);
            
            // 12 sec
            setTimeout(() => {
                this.$router.push('/escalier');
            }, 12000);

            // La musique
            this.decreaseSong();
        },

        decreaseSong() {
            let timer = setInterval(() => {
                if (this.song.volume - 0.1 < 0) {
                    clearInterval(timer);
                } else {
                    this.song.volume -= 0.1;
                }
            }, 1000)
        },

        fermerInstructions() {
            this.instructionsLues = true;
        },

        resizeImage() {
            var image = document.getElementById("imgChateau");
    
            if (image.offsetWidth > window.innerWidth) {
                image.style.width = "100%";
                image.style.height = "auto";
            
                var container = image.parentElement;
                container.style.width = "100%";        
                container.style.height = "max-content"; 
            }       
        }
    },

    unmouted() {
        this.song.pause();
    },

    created() {
        this.song.play();
    }
}