import Password from "@/components/password/password.vue"
export default {
    name: "BlocInvisible",
    data() {
        return {
            bloc: null,
            displayModal: false,
            isOuvert: false,
        }
    },
    emits: ["password-found-event", "premiere-ouverture-event"],
    components: {
        Password
    },
    props: {
        hauteur: {},
        largeur: {},
        top: {},
        left: {},
        message: {},
        idBloc: {},
        motDePasse: {
            default: null
        },
        grosseDame: {
            default: false,
            type: Boolean
        },
    },
    created() {
        window.addEventListener("resize", this.placerBloc);
    },
    mounted() {
        this.bloc = document.getElementById(this.idBloc)
        this.placerBloc();
    },
    unmounted() {
        window.removeEventListener("resize", this.placerBloc);
    },
    methods: {
        placerBloc() {
            this.bloc.style.width = this.largeur + "%";
            this.bloc.style.height = this.hauteur + "%";
            this.bloc.style.top = this.top + "%";
            this.bloc.style.left = this.left + "%";
        },

        toggleModal() {
            this.displayModal = !this.displayModal;
            if (this.displayModal) {
                this.firstOpen();
            }
        },

        firstOpen() {
            if (!this.isOuvert) {
                this.$emit("premiere-ouverture-event");
                this.isOuvert = true;
            }
        },

        passwordFound() {
            this.$emit("password-found-event");
        }
    }
}